import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
/**
 * Componente Icon Button. 
 * El Icon Button es un accionable que sintetiza y resalta
 * una funcionalidad dentro de una interfaz.
 *
 * @export
 * @class BcIconButtonComponent
 */
@Component({
  selector: 'bc-icon-button',
  templateUrl: 'bc-icon-button.component.html',
})
export class BcIconButtonComponent implements OnChanges {
  classList: string = '';
  ariaLabelFabSelected: string = 'Cerrar';
  ariaLabelValue: string;

  /**
   * Parámetro para denotar el tipo de botón
   * @type {('fab' | 'solid' | 'outline' | 'ghost')}
   * @memberof BcIconButtonComponent
   */
  @Input() type: 'fab' | 'solid' | 'outline'| 'outline-inverse' | 'ghost';

  /**
   * Parámetro para denotar el color de botón
   * @type {('yellow' | 'black' | 'white')}
   * @memberof BcIconButtonComponent
   */
  @Input() color: 'yellow' | 'black' | 'white';

  /**
   * Parámetro para denotar el tamaño del botton
   * @type {('xsmall' | 'small' | 'medium' | 'large'| 'xlarge')}
   * @memberof BcIconButtonComponent
   */
  @Input() size: 'xsmall' | 'small' | 'medium' | 'large' | 'xlarge';

  /**
   * Parámetro para denotar el shadow del botton en variante Solid
   * @type {boolean}
   * @memberof BcIconButtonComponent
   */
  @Input() shadow: boolean = false;

  /**
   * Parámetro para el custom icon del componente.
   * @type {string}
   * @memberof BcIconButtonComponent
   */
  @Input() customIcon: string = '';

  /**
   * Desabilitar button
   * @type {boolean}
   * @memberof BcIconButtonComponent
   */
  @Input() statusButton = false;

  /**
   * Por deprecar. Utilizar 'selected'
   * @type {boolean}
   * @memberof BcIconButtonComponent
   */
  @Input() seleted = false;

  /**
   * Parámetro para poner el componente en un estado seleccionado.
   * @type {boolean}
   * @memberof BcIconButtonComponent
   */
  @Input() selected = false;

  /**
   * Parámetro para definir aria-label
   * @type {string}
   * @memberof BcIconButtonComponent
   */
  @Input() ariaLabel: string;

  ngOnChanges(changes: SimpleChanges): void {
    if (this.type === 'solid') {
      const newColor = changes.color?.currentValue || this.color;
      this.color = newColor || 'white';
      this.classList = `bc-icon-button-${this.color}`;
    } else {
      this.classList = '';
    }
    if (changes.selected || changes.type) {
      this.updateAriaLabel();
    }
  }

  private updateAriaLabel(): void {
    if (this.type === 'fab' && (this.selected || this.seleted)) {
      this.ariaLabelValue = this.ariaLabelFabSelected;
    } else {
      this.ariaLabelValue = this.ariaLabel;
    }
  }
}
